.big-icon {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: flex-start;
  padding: 2em;
  color: $color-base;

  @include breakpoint(small) {
    &:nth-child(odd) {
      background-color: $color-gray-lightest;
    }
  }

  @include breakpoint(medium) {
    &:nth-child(odd) {
      border-right: $global-border-width solid $global-border-color;
    }

    &:nth-child(4n-3),
    &:nth-child(4n-2) {
      background-color: $color-gray-lightest;
    }

    &:nth-child(4n-1),
    &:nth-child(4n) {
      background-color: transparent;
    }
  }

  @include breakpoint(large) {
    &:nth-child(odd) {
      border-right: 0;
    }

    &:nth-child(3n-2),
    &:nth-child(3n-1) {
      border-right: $global-border-width solid $global-border-color;
    }

    &:nth-child(6n-5),
    &:nth-child(6n-4),
    &:nth-child(6n-3) {
      background-color: $color-gray-lightest;
    }

    &:nth-child(6n-2),
    &:nth-child(6n-1),
    &:nth-child(6n) {
      background-color: transparent;
    }
  }

  .main-content & {
    text-decoration: none;
  }

  .path-dark-gray {
    fill: $color-base;
  }

  &:active,
  &:focus,
  &:hover {
    background-color: $color-primary;
    color: $white;

    h3 {
      background-color: $color-primary;
      color: $white;
    }

    .path-dark-gray {
      fill: $color-gray-lighter;
    }
  }

  svg {
    display: block;
    max-width: 90%;
    max-height: 10em;
    margin: 2em auto;
  }

  h3 {
    margin: 0;
    color: $color-gray-dark;
    font-size: 1.75rem;
    text-align: center;
  }

  p {
    margin: 0 auto;
    text-align: center;
  }
}

.columns > .home-content:only-child {
  margin: -1em;
}

.row {
  .home-content.row {
    margin: -1.5em;

    @include breakpoint(medium) {
      margin: -2em;
    }
  }

  .home-logo.row {
    margin-top: -2em;
  }
}
