::selection {
  background: $color-secondary-darkest;
  color: $white;
}

*,
*::before,
*::after {
  font-smoothing: antialiased;
  text-rendering: optimizelegibility;
  box-sizing: border-box;
}

[ng\:cloak],
[ng-cloak],
[data-ng-cloak],
[x-ng-cloak],
.ng-cloak,
.x-ng-cloak {
  display: none;
}

a,
button,
input,
path,
polygon,
.button {
  transition: all 0.5s ease;
}

button,
input,
optgroup,
select,
textarea {
  color: $color-base;
}

a {
  color: $color-primary;

  &:active,
  &:focus,
  &:hover {
    color: $color-secondary-darkest;
  }

  &[href^='http://'],
  &[href^='https://'],
  &[href^='mailto'],
  &[href$='.pdf'] {
    margin-left: 0.5em;
    padding-left: 1.5em;
    background-image: url('../images/icons/link-external.svg');
    background-repeat: no-repeat;
    background-position: center left;
    background-size: 1em 1em;
  }

  &[href^='mailto:'] {
    background-image: url('../images/icons/link-email.svg');
  }

  &[href$='.pdf'] {
    background-image: url('../images/icons/link-pdf.svg');
  }
}

blockquote,
h2,
h3,
h4,
h5,
h6,
li,
p {
  max-width: 40rem;
}

p,
li {
  line-height: 1.5;
}

svg {
  display: inline-block;
}

.article-question {
  margin-bottom: 1.5em;

  h3 {
    font-size: 1.2em;
  }

  .answer {
    font-style: italic;
  }
}

.flex-col-sm {
  @include breakpoint(small only) {
    flex-direction: column;
  }
}

.input-group-button button {
  padding: 1em;

  @include breakpoint(medium) {
    padding: 0.85em 1em;
  }
}

.column-border {
  width: 4.5rem;

  &:first-child {
    width: calc(4.5rem + 1px);
    border-right: $global-border-width solid $global-border-color;
  }
}

.columns.columns-content {
  padding: 1.5em;

  @include breakpoint(medium) {
    padding: 2em;
  }
}

.fade.ng-enter {
  transition: 0.5s linear all;
  opacity: 0;

  &.ng-enter-active {
    opacity: 1;
  }
}

.icon-list {
  margin-left: 0;
  list-style: none;

  li {
    margin-bottom: $global-margin;
    line-height: 2em;
  }

  .icon-list-icon {
    float: left;

    svg {
      width: 2em;
      height: 2em;
      margin-right: 1em;
    }
  }
}

.link-icon {
  position: relative;
  top: 0.1em;
  width: 0.8em;
  height: 0.8em;
  margin-right: 0.25em;
}

.main-content {
  margin-top: 54px;
  transition: margin-top 0.5s ease;

  a {
    text-decoration: underline;
  }

  h2,
  h4,
  h6 {
    color: $color-secondary-darkest;
  }

  .article-single {
    max-width: 40rem;
    margin-bottom: $global-margin;

    + .article-single {
      margin-top: $global-margin;
      padding-top: $global-padding;
      border-top: $global-border-width solid $global-border-color;
    }

    h3 {
      position: relative;
      margin-bottom: 0;

      .link-icon-container {
        position: relative;
        left: -2rem;
        margin-right: -1.5rem;
        float: left;
      }

      a {
        &[href^='http://'],
        &[href^='https://'],
        &[href^='mailto'],
        &[href$='.pdf'] {
          margin-left: 0;
          padding-left: 0;
          background: none;
        }

        &[href^='http://'],
        &[href^='https://'] {
          .link-icon-external {
            display: inline;
          }

          .link-icon-email,
          .link-icon-pdf {
            display: none;
          }
        }

        &[href^='mailto'] {
          .link-icon-email {
            display: inline;
          }

          .link-icon-external,
          .link-icon-pdf {
            display: none;
          }
        }

        &[href$='.pdf'] {
          .link-icon-pdf {
            display: inline;
          }

          .link-icon-external,
          .link-icon-email {
            display: none;
          }
        }

        .link-icon {
          .path-red {
            fill: $color-primary;
          }
        }

        &:active,
        &:focus,
        &:hover {
          .link-icon {
            .path-red {
              fill: $color-secondary-darkest;
            }
          }
        }
      }
    }

    h4 {
      margin-bottom: 0;
      color: $color-base;
      font-size: 0.8em;
      font-weight: $global-weight-bold;
    }

    p {
      margin-bottom: 0;
    }

    ul {
      margin-left: 2em;
    }

    .article-single-brief {
      margin-top: 0.5em;
    }

    .article-single-source {
      font-size: 0.8em;
      font-style: italic;
      font-weight: $global-weight-normal;
    }
  }

  .section-single + .section-single {
    margin-top: 2rem;
  }
}

.max-global {
  position: relative;
  width: 100%;
  min-height: 100vh;
  background-color: $content-background;
  z-index: 1;

  @include breakpoint(medium) {
    max-width: 100%;
  }
}

.navbar,
.sidebar {
  section {
    padding: 1em;
  }

  form {
    button,
    input,
    select,
    textarea {
      width: 100%;
    }
  }

  article {
    margin-bottom: 2em;
  }

  time {
    display: block;
    margin: 0 0 0.5em;
  }
}

.search-results {
  margin-top: 1em;

  h2:last-child {
    display: none;
  }

  .article-single {
    margin-left: 0;
  }
}

.show-for-sr {
  width: 0;
  height: 0;
}

.show-on-focus {
  display: block;
  padding: 1em;
  color: $white;
  font-weight: $global-weight-bold;
  text-align: center;

  &:active,
  &:focus,
  &:hover {
    color: $white;
  }
}

.tooltip {
  font-family: $alt-font-family;
  font-size: 14px;
  font-weight: $global-alt-weight-bold;
  text-transform: uppercase;
}
