.icon-menu {
  position: relative;
  background-color: $color-secondary-darkest;
  z-index: 2;

  @include breakpoint(small only) {
    padding-bottom: 4em;
  }

  @include breakpoint(medium) {
    position: absolute;
    right: 0;
    width: 4.5em;
    min-height: 100%;
  }

  > nav {
    width: 100%;

    @include breakpoint(small only) {
      display: flex;
      position: absolute;
    }
  }

  a {
    display: block;
    width: 100%;
    padding: $global-padding;
    border-bottom: 0;
    color: $white;
    font-family: $alt-font-family;
    font-size: 14px;
    font-weight: $global-alt-weight-bold;
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;

    @include breakpoint(small only) {
      flex: 1 1 auto;
    }

    @include breakpoint(medium) {
      width: 100%;
      margin: $global-margin 0 0;
    }

    &:active,
    &:focus,
    &:hover {
      transform: scale(1.2);
      color: $color-primary-alt-light;
      animation: hoverscale 1s 1 ease-in-out;

      .path-blue,
      .path-dark-gray,
      .path-light-gray,
      .path-red,
      .path-white {
        fill: $color-primary-alt-light;
      }

      .path-transparent {
        fill: $color-secondary-darkest;
      }
    }

    svg {
      width: 1.5rem;
      height: 1.5rem;

      @include breakpoint(medium) {
        width: 2.5rem;
        height: 2.5rem;
      }
    }

    .path-blue,
    .path-dark-gray,
    .path-light-gray,
    .path-red {
      fill: $white;
    }

    .path-transparent {
      fill: $color-secondary-darkest;
    }
  }
}
