body {
  height: 100%;
  min-height: 100vh;
  overflow-y: visible;
}

.off-canvas-wrapper {
  min-height: 100vh;
  background-color: $offcanvas-left-background;
  overflow: hidden;
}

.off-canvas-content {
  min-height: 100vh;
  outline: 0;
  overflow-y: visible;

  > .row {
    margin-right: auto;
    margin-left: 0;
  }
}

.js-off-canvas-exit {
  width: 0;
  height: 0;
}

.off-canvas {
  position: absolute;
  border-width: $offcanvas-top-border-size $offcanvas-left-border-size 0 0;
  border-style: solid;
  border-color: $offcanvas-left-border-color;
  outline: 0;
  background-color: $offcanvas-left-background;
  color: $offcanvas-left-color;
  font-size: 0.9em;
  z-index: -1;
  overflow-y: auto;

  &.position-right {
    border-width: $offcanvas-top-border-size 0 0 $offcanvas-right-border-size;
    border-color: $offcanvas-right-border-color;
    background-color: $offcanvas-right-background;
    color: $offcanvas-right-color;

    a {
      color: $offcanvas-right-link;

      &:active,
      &:focus,
      &:hover {
        color: $offcanvas-right-link-active;
      }
    }

    .off-canvas-single {
      a {
        text-decoration: underline;

        &[href^='http://'],
        &[href^='https://'],
        &[href$='.pdf'] {
          padding-right: 0;
          background: none;
        }

        .link-icon {
          .path-black {
            fill: $white;
          }

          .path-red {
            fill: $offcanvas-right-link;
          }
        }

        &:active,
        &:focus,
        &:hover {
          .link-icon {
            .path-black {
              fill: $white;
            }

            .path-red {
              fill: $offcanvas-right-link-active;
            }
          }
        }
      }

      time {
        font-family: $alt-font-family;
        font-size: 1em;
        font-weight: $global-alt-weight-bold;
      }
    }
  }

  a {
    color: $offcanvas-left-link;

    &:active,
    &:focus,
    &:hover {
      color: $offcanvas-left-link-active;
    }
  }

  h2 {
    font-size: 1.5em;
  }

  h3 {
    font-size: 1.2em;
  }
}
