.columns {
  &.content-sidebar {
    padding: 0 1.5rem 1.5rem;
    font-size: 0.75em;

    @include breakpoint(medium) {
      padding: 2rem 2rem 2rem 0;
    }

    &:empty {
      display: none;
    }

    a {
      width: 100%;
      border-top: $global-border-width solid $global-border-color;
      color: $color-base;

      .row {
        transition: all 0.5s ease;
      }

      .sidebar-column-icon {
        display: flex;
        flex-direction: column;
        justify-content: center;
        transition: all 0.5s ease;
        background-color: $color-gray-light;
      }

      &:nth-of-type(even) {
        .sidebar-column-icon {
          background-color: $color-gray-lightest;
        }

        .path-white {
          fill: $color-gray;
        }
      }

      &:active,
      &:focus,
      &:hover {
        .row {
          background-color: $color-secondary-darkest;
          color: $white;
        }

        .sidebar-column-icon {
          background-color: $color-secondary-darkest;
        }

        .path-white {
          fill: $white;
        }
      }

      &:last-of-type {
        border-bottom: $global-border-width solid $global-border-color;
      }
    }

    h3 {
      font-size: 1.25em;
    }

    svg {
      max-width: 100%;
    }
  }
}
