.button {
  margin-bottom: 0;
  font-family: $alt-font-family;
  font-size: 1em;
  font-weight: $global-alt-weight-bold;
  line-height: 1.25;

  &.secondary {
    background-color: $color-gray-dark;

    &:active,
    &:focus,
    &:hover {
      background-color: darken($color-gray-dark, 15%);
    }
  }

  &.button-info {
    position: relative;
    padding: 0;
    border: $global-border-width solid $color-gray-lighter;
    background-color: $white;
    color: $color-primary;
    line-height: 3em;
    text-align: left;
    text-transform: uppercase;

    &.button-ask {
      background-color: transparent;
      color: $color-gray-dark;
      font-family: $body-font-family;
      font-weight: $global-weight-normal;
      text-transform: none;
    }

    &:active,
    &:focus,
    &:hover {
      background-color: transparent;

      .button-info-icon {
        width: 100%;
      }
    }

    + .button-info {
      margin-top: 1em;
    }

    a {
      text-decoration: none;
    }

    .button-info-icon {
      position: absolute;
      right: 0;
      width: 1.5em;
      height: 1.5em;
      padding: 0.25em;
      transition: all 0.5s ease;
      background-color: $color-gray-dark;
      font-size: 2em;
      z-index: 2;

      svg {
        display: block;
        max-width: 100%;
        max-height: 100%;
        margin: 0 auto;
      }
    }

    .button-info-title {
      position: relative;
      padding: 0 4em 0 1em;
      z-index: 1;
    }

    &.alt {
      &,
      a {
        color: $color-primary-alt-darker;
      }

      .button-info-icon {
        background-color: $color-primary-alt-darker;
      }
    }

    &.secondary {
      &,
      a {
        color: $color-gray-dark;
      }

      .button-info-icon {
        background-color: $color-gray-dark;
      }
    }

    &.success {
      &,
      a {
        color: $color-green;
      }

      .button-info-icon {
        background-color: $color-green;
      }
    }

    &.warning {
      &,
      a {
        color: $color-orange;
      }

      .button-info-icon {
        background-color: $color-orange;
        color: $color-gray-dark;
      }
    }

    &.alert {
      &,
      a {
        background-color: $color-secondary-darkest;
        color: $white;
      }

      .button-info-icon {
        background-color: $white;
        color: $color-secondary-darkest;
      }
    }
  }
}

.button-close {
  position: relative;
  width: 4.5em;
  height: 4.5em;
  background-color: $color-gray;
  font-size: 0.7em;
  line-height: 1;

  span {
    display: block;
    position: absolute;
    top: 2em;
    width: 2.5em;
    height: 0.5em;
    border-radius: 0.5em;
    background: $white;

    &:nth-child(1) {
      transform: rotate(45deg);
    }

    &:nth-child(2) {
      transform: rotate(-45deg);
    }
  }
}

.button-icon {
  padding: 0.5em 0.75em;

  svg {
    width: 1.5em;
    height: auto;
  }
}
