.row-footer {
  background-color: $color-gray-dark;
  color: $white;
  font-size: 0.8em;
  text-align: center;

  > .columns > *,
  > .columns > *:only-child {
    margin-right: auto;
    margin-left: auto;

    &:last-child {
      margin-bottom: 0;
    }
  }

  a {
    color: $color-primary-alt-light;

    &:active,
    &:focus,
    &:hover {
      color: $white;
    }
  }

  span {
    > blockquote,
    > h2,
    > h3,
    > h4,
    > h5,
    > h6,
    > p,
    > ul li {
      margin-right: auto;
      margin-left: auto;

      &:only-child {
        margin-right: auto;
        margin-left: auto;
      }
    }
  }

  ul {
    margin: 0;
    list-style: none;
  }

  .column-border:first-child {
    border-right: $global-border-width solid $color-gray;
  }
}
