.header-button {
  padding: 0;

  > button {
    display: block;
    position: relative;
    width: 1.5em;
    height: 1.5em;
    padding: 0.35em;
    transform: rotate(0deg);
    transition: 0.5s ease;
    font-size: 3em;
    line-height: 1;
  }
}

.header-button-menu {
  display: flex;
  position: relative;
  width: 4.5em;
  height: 4.5em;
  border-right: $global-border-width solid $color-gray;
  background-color: $color-gray;
  z-index: 2;

  > button {
    width: 4.5em;
    height: 4.5em;
    transition: all 0s;
    background-color: $color-gray;
    font-size: 1em;

    &:active,
    &:focus,
    &:hover,
    &.open {
      span {
        background-color: $color-primary-alt-light;
      }
    }

    &[aria-expanded='true'] span {
      &:nth-child(1),
      &:nth-child(4) {
        top: 2em;
        left: 50%;
        width: 0%;
      }

      &:nth-child(2) {
        transform: rotate(45deg);
      }

      &:nth-child(3) {
        transform: rotate(-45deg);
      }
    }

    span {
      display: block;
      position: absolute;
      left: 1em;
      width: 2.5em;
      height: 0.5em;
      transform: rotate(0deg);
      transition: 0.5s ease;
      border-radius: 0.5em;
      background: $white;
      opacity: 1;

      &:nth-child(1) {
        top: 1em;
      }

      &:nth-child(2),
      &:nth-child(3) {
        top: 2em;
      }

      &:nth-child(4) {
        top: 3em;
      }
    }
  }
}

.header-main {
  svg {
    max-width: 100%;
  }
}

.header-top {
  font-size: 0.85em;

  @include breakpoint(medium) {
    font-size: 1em;
  }
}

.official-website {
  border-bottom: $global-border-width solid $color-gray;
  background-color: $color-gray-dark;
  color: $white;
  font-family: $alt-font-family;
  font-size: 11px;
  font-weight: $global-alt-weight-normal;
  line-height: 20px;
  text-transform: uppercase;

  .tagline {
    padding: 0.5em 1.75em 0.5em 4em;
    background-image: url('../images/icons/flag.svg');
    background-repeat: no-repeat;
    background-position: 1.25em center;
    background-size: 21px 21px;
  }
}

.row-header {
  background-color: $color-gray-dark;
  color: $white;

  &.sticky {
    position: fixed;
    width: 100%;
    max-height: 400px;
    transition: max-height 0.5s ease;
  }

  a {
    color: $white;
    text-decoration: underline;

    &:active,
    &:focus,
    &:hover {
      color: $color-primary-alt-light;
    }
  }

  h1 {
    margin-bottom: 1rem;
    font-size: 4em;
    line-height: 1;

    a {
      text-decoration: none;
    }
  }

  svg {
    display: block;
  }

  .header-bar h1 {
    margin: 0 1rem;
    font-size: 3em;
  }

  .home-header-button {
    width: 100%;
    font-family: $alt-font-family;
    font-size: 0.9em;
    font-weight: $global-alt-weight-normal;
    text-align: left;

    &:active,
    &:focus,
    &:hover {
      .path-white {
        fill: $color-primary-alt-light;
      }
    }

    :last-child {
      margin-bottom: 0;
    }

    a {
      text-decoration: none;
    }

    h2 {
      margin-bottom: 0.5rem;
      font-family: $alt-font-family;
      font-size: 1.5em;
      font-weight: $global-alt-weight-normal;
    }
  }
}
