// String Replacement function

@function str-replace($string, $search, $replace: '') {
  $index: str-index($string, $search);
  @if $index {
    @return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);
  }
  @return $string;
}


// Font Face Definition Mixin

@mixin fontdef($family, $type, $weight: normal, $style: normal, $stretch: normal) {
  @font-face {
    font-family: '#{$family}';
    src: url('#{$font-path}/#{str-replace(to-lower-case(inspect($family)), ' ')}-#{$type}.woff2') format('woff2'),
         url('#{$font-path}/#{str-replace(to-lower-case(inspect($family)), ' ')}-#{$type}.woff') format('woff');
    font-weight: #{$weight};
    font-style: #{$style};
    font-stretch: #{$stretch};
  }
}


// Loop through font families to generate @font-face declarations

@each $family, $data in $font-families {
  @each $type, $weight, $style, $stretch in $data {
    @include fontdef($family, $type, $weight, $style, $stretch);
  }
}
