.loader,
.loader::after {
  width: 3em;
  height: 3em;
  border-radius: 50%;
}

.loader {
  position: relative;
  margin: 1em auto;
  transform: translateZ(0);
  border-top: 0.5em solid $color-secondary-darkest-t;
  border-right: 0.5em solid $color-secondary-darkest-t;
  border-bottom: 0.5em solid $color-secondary-darkest-t;
  border-left: 0.5em solid $color-secondary-darkest;
  text-indent: -9999em;
  overflow: hidden;
  animation: loader 1s infinite linear;

  .header-button-menu &,
  .header-main &,
  .off-canvas & {
    border-top: 0.5em solid $color-white-t;
    border-right: 0.5em solid $color-white-t;
    border-bottom: 0.5em solid $color-white-t;
    border-left: 0.5em solid $white;
  }

  .header-button-menu & {
    margin-top: 0.75em;
  }
}
