// Borders

.border-none {
  border: 0;
  box-shadow: none;
}

.border-all {
  border: $global-border-width solid $global-border-color;
}

.border-top {
  border-top: $global-border-width solid $global-border-color;
}

.border-right {
  border-right: $global-border-width solid $global-border-color;
}

.border-bottom {
  border-bottom: $global-border-width solid $global-border-color;
}

.border-left {
  border-left: $global-border-width solid $global-border-color;
}

.grid-border {
  border: 0;
  box-shadow: inset 0 $global-border-width 0 0 $global-border-color,
              inset $global-border-width 0 0 0 $global-border-color; // sass-lint:disable-line indentation

  > * {
    box-shadow: inset -$global-border-width 0 0 0 $global-border-color,
                inset 0 -$global-border-width 0 0 $global-border-color; // sass-lint:disable-line indentation, space-around-operator
  }
}


// Margins

.margin-none {
  margin: 0;
}

.margin-top-none {
  margin-top: 0;
}

.margin-right-none {
  margin-right: 0;
}

.margin-bottom-none {
  margin-bottom: 0;
}

.margin-left-none {
  margin-left: 0;
}

.margin-all {
  margin: $global-margin;
}

.margin-top {
  margin-top: $global-margin;
}

.margin-right {
  margin-right: $global-margin;
}

.margin-bottom {
  margin-bottom: $global-margin;
}

.margin-left {
  margin-left: $global-margin;
}

.margin-double-all {
  margin: 2em;
}

.margin-double-top {
  margin-top: 2em;
}

.margin-double-right {
  margin-right: 2em;
}

.margin-double-bottom {
  margin-bottom: 2em;
}

.margin-double-left {
  margin-left: 2em;
}


// Padding

.padding-none {
  padding: 0;
}

.padding-top-none {
  padding-top: 0;
}

.padding-right-none {
  padding-right: 0;
}

.padding-bottom-none {
  padding-bottom: 0;
}

.padding-left-none {
  padding-left: 0;
}

.padding-all {
  padding: $global-padding;
}

.padding-top {
  padding-top: $global-padding;
}

.padding-right {
  padding-right: $global-padding;
}

.padding-bottom {
  padding-bottom: $global-padding;
}

.padding-left {
  padding-left: $global-padding;
}

.padding-double-all {
  padding: 2em;
}

.padding-double-top {
  padding-top: 2em;
}

.padding-double-right {
  padding-right: 2em;
}

.padding-double-bottom {
  padding-bottom: 2em;
}

.padding-double-left {
  padding-left: 2em;
}


// Text

.text-smallest {
  font-size: 0.7em;
}

.text-smaller {
  font-size: 0.8em;
}

.text-small {
  font-size: 0.9em;
}

.text-large {
  font-size: 1.1em;
}

.text-larger {
  font-size: 1.2em;
}

.text-largest {
  font-size: 1.3em;
}
