.header-icon {
  svg {
    @include breakpoint(small only) {
      display: inline-block;
      width: 1em;
      height: 1em;
      margin-right: 0.25em;
    }
  }

  .path-blue {
    fill: $white;
  }

  .path-red {
    fill: $color-secondary;
  }

  .path-white {
    fill: $color-gray-dark;
  }
}

.header-icon-report-a-cyber-incident {
  .path-white {
    fill: $white;
  }
}
