.columns {
  padding: 1em;

  > :only-child {
    margin: 0;
  }
}

.row {
  align-content: center;
  align-items: stretch;

  &.row-grow {
    flex-grow: 1;
    align-content: stretch;
  }

  .row {
    margin: 0;

    &.row-centered {
      max-width: $global-width-px;
      margin: 0 auto;
    }
  }
}
